 .App-header {
    background-color: #2B2E31;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(8px + 1vmin);
    color: white;
}

.nav-bar {
    position: absolute;
    top: 0;
    width: 100%;
    background-color: #212427; 
    padding: 20px;
    border-bottom-color: #2894D8;
    border-bottom-style: solid;
    border-bottom-width: 1px;
}

.nav-bar-list {
    list-style: none;
    margin: 0;
    display: flex;
    justify-content: space-evenly;
    font-family: Arial, Helvetica, sans-serif;
}

.nav-bar-items {
    cursor: pointer;
}

.nav-bar-items:hover {
    color: #02D5D5;
}

 body{
    height: 100vh;
    background: radial-gradient(ellipse at bottom, #1B2735 0%, #090A0F 100%);
    overflow: hidden
 }

.star{
    position: absolute;
    background: white;
    border-radius: 50px;
    animation: move;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.type-1{
    width: 1px;
    height: 1px;
}
.type-2{
    width: 2px;
    height: 2px;
}
.type-3{
    width: 3px;
    height: 3px;
}

@keyframes move {
    0%{ transform: translateY(0); }
    100%{ transform: translateY(-1000px); }
}